import React, {useEffect, useState} from 'react';
import {Switch, Route} from 'react-router-dom';
import styles from './styles.module.scss';
import Main from './tabs/Main';
import Footer from './Footer';
import Menu, {Paths} from './Menu';
import AboutGlobal from './tabs/AboutTab';
import Career from './tabs/Career';
import AwardsGlobal from './tabs/FundTab/AwardsTab';
import WhyUsTab from './tabs/FundTab/WhyUsTab';
import AdvantagesGlobal from './tabs/FundTab/AdvantagesTab';
import LoginTab from './tabs/LoginTab';
import RegisterTab from './tabs/RegisterTab';
import Contacts from './tabs/Contacts';
import Assistant from "./tabs/ServiceTab/Assistant";
import Portfel from "./tabs/ServiceTab/Portfel";
import Fund from "./tabs/ServiceTab/Fund";
import ClientAgreement from "./tabs/FundTab/ClientAgreement";
import AntiMoneyLaundering from "./tabs/FundTab/AntiMoneyLaundering";
import PrivacyPolicy from "./tabs/FundTab/PrivacyPolicy";
import RegistrationCertificate from "./tabs/FundTab/RegistrationCertificate";
import Declaration from "./tabs/FundTab/Declaration";
import Calendar from "./tabs/FundTab/Calendar";
import {useLocation} from 'react-router-dom';

function Landing() {
  const {pathname} = useLocation();

  const [runnerContainer, setRunnerContainer] = useState(null);
  const [calendarContainer, setCalendarContainer] = useState(null);

  // after we get runner container... (see Runner)
  // we can hide it when it's not supposed to be displayed
  useEffect(() => {
    if (!runnerContainer) return;
    if (pathname === Paths.MAIN) {
      runnerContainer.classList.remove('d-none');
    } else {
      runnerContainer.classList.add('d-none');
    }
  }, [runnerContainer, pathname]);

  // and the same for calendar
  useEffect(() => {
    if (!calendarContainer) return;
    if (pathname === Paths.CALENDAR) {
      calendarContainer.classList.remove('d-none');
    } else {
      calendarContainer.classList.add('d-none');
    }
  }, [calendarContainer, pathname]);

  return (
    <>
      <Menu/>
      <div className={styles.container}>
        <Switch>
          <Route path={Paths.MAIN} exact component={() => <Main setRunnerContainer={setRunnerContainer}/>}/>
          <Route path={Paths.ABOUT} component={AboutGlobal}/>

          <Route path={Paths.AWARDS} component={AwardsGlobal}/>
          <Route path={Paths.WHY_US} component={WhyUsTab}/>
          <Route path={Paths.ADVANTAGES} component={AdvantagesGlobal}/>
          <Route path={Paths.CLIENT_AGREEMENT} component={ClientAgreement}/>
          <Route path={Paths.PRIVACY_POLICY} component={PrivacyPolicy}/>
          <Route path={Paths.ANTI_MONEY_LAUNDERING} component={AntiMoneyLaundering}/>
          <Route path={Paths.DECLARATION} component={Declaration}/>
          <Route path={Paths.REGISTRATION_CERTIFICATE} component={RegistrationCertificate}/>
          <Route path={Paths.CALENDAR} component={() => <Calendar setContainer={setCalendarContainer}/>}/>

          <Route path={Paths.ASSISTANT} component={Assistant}/>
          <Route path={Paths.PORTFEL} component={Portfel}/>
          <Route path={Paths.FUND} component={Fund}/>

          <Route path={Paths.CAREER} component={Career}/>
          <Route path={Paths.CONTACTS} component={Contacts}/>

          <Route path={Paths.LOGIN} component={LoginTab}/>
          <Route path={Paths.REGISTER} component={RegisterTab}/>
        </Switch>
        <Footer/>
      </div>
    </>
  );
}

export default Landing;
