import React from "react";
import FileViewPage from "../../../FileViewPage";
import bg from '../../Career/images/bg.png';

function ClientAgreement() {
  return (
    <FileViewPage
      title="КЛИЕНТСКОЕ СОГЛАШЕНИЕ"
      headerBgImage={bg}
      filePath={require('../../../../../docs/Client_Agreement.pdf')}
      fileType="pdf"
    />
  );
}

export default ClientAgreement;
