import React from 'react';
import Header from '../../../Header';
import styles from './styles.module.scss';
import Awards from '../../Main/components/Awards';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';

function AwardsGlobal() {
  return (
    <>
      <Header
        title="НАГРАДЫ И ДОСТИЖЕНИЯ"
        className={styles.header}
      />
      <Awards/>
      <Container className={styles.container}>
        {/*<div className={styles.subheader}>*/}
        {/*  ВОТ НЕКОТОРЫЕ ИЗ НАШИХ РЕЗУЛЬТАТОВ*/}
        {/*</div>*/}
        <Row>
          <Col xs={12} lg={6} className="flex-column px-3 py-2">
            <AdvantagesIconList/>
            <div className="font-weight-bold">
              Благодаря этим критериям Фонд завоевал почетную награду на премии Global Invest 2019 в Санкт-Петербурге и был номинирован на звание Лучшая Программа Лояльности (Best Loyalty Program) на международном форуме Trading Expo 2019 Cyprus.
            </div>
          </Col>
          <Col xs={12} lg={6} className="flex-column px-3 py-2">
            <div>
              Сегодня мы занимаем лидирующие позиции в области формирования инвестиционных портфелей и выявления самых развивающихся секторов и корпораций всего мира. Благодаря этому в 2019 году, мы были признаны Самым быстрорастущим Инвестиционным Фондом по версии журнала Daily Trade. В 2019 году, сумма активов под управлением фонда увеличилась до 268 млн. евро, что означало прирост притока инвестиций на 58% по сравнению с 2018г.
            </div>
            <PollList/>
          </Col>
        </Row>
        <div className="font-weight-bold text-center px-2">
          Спасибо что вы с нами! Мы продолжим удивлять Вас наилучшими результатами.
        </div>
      </Container>
    </>
  );
}

export function PollList() {
  function ListItem({number, text}) {
    return (
      <div className={styles.pollItem}>
        <div className={styles.number}>{number}%</div>
        <div className={styles.text}>{text}</div>
      </div>
    );
  }

  return (
    <div className={styles.pollList}>
      Опрос показал:
      <ListItem
        number={90}
        text="Клиентов Нашего Фонда планируют долгосрочное сотрудничество с Фондом"
      />
      <ListItem
        number={28}
        text="Клиентов Нашего фонда рассматривали Банковский депозит как привлекательное вложение. Спустя год сотрудничества, 100% клиентов признали банковский депозит неконкурентоспособным в сравнении с доходами поступающих от Нашего Фонда."
      />
      <ListItem
        number={87}
        text="Инвесторов Нашего Фонда считают доходы поступающие от инвестиций в Наш Фонд, конкурентоспособными с их основной сферой деятельности."
      />
    </div>
  );
}

export function AdvantagesIconList({flex}) {
  function ListItem({text}) {
    return (
      <div className={styles.advantageItem}>
        <Image src={require('./images/icon.png')} className={styles.icon}/>
        <div className={styles.text}>{text}</div>
      </div>
    );
  }

  return (
    <>
      <div>
        Нашей первой задачей стало, построение критериев работы базирующихся на основных критериях развития Европейского фонда Инвестиций, а именно:
      </div>
      <Row className={styles.advantageList}>
        <Col xs={12} lg={flex ? 6 : 12} className="d-block pr-3">
          <ListItem
            text="Максимальная клиентоориентированность"
          />
          <ListItem
            text="Максимальная устойчивость продукта на рынке"
          />
          <ListItem
            text="Разнообразие эффективных стратегий и финансовых инструментов для осуществления потребностей каждого инвестора"
          />
        </Col>
        <Col xs={12} lg={flex ? 6 : 12} className="d-block pr-3">
          <ListItem
            text="Надежность хранения и размещения активов"
          />
          <ListItem
            text="Прозрачность проводимых операций"
          />
          <ListItem
            text="Полное онлайн сопровождение на весь срок сотрудничества"
          />
        </Col>
      </Row>
    </>
  );
}

export default AwardsGlobal;
