import React, {useState} from "react";
import { useEffect } from 'react';
import useScript from "../../../common/hooks/useScript";

function Runner({setContainer}) {
  const [loaded] = useScript(
    'https://s3.tradingview.com/external-embedding/embed-widget-ticker-tape.js',
    `{
          "symbols": [{
          "proName": "OANDA:SPX500USD",
          "title": "S&P 500"
        },{
          "proName": "OANDA:NAS100USD",
          "title": "NASDAQ 100"
        },{
          "proName": "FX_IDC:EURUSD",
          "title": "EUR/USD"
        },{
          "proName": "BITSTAMP:BTCUSD",
          "title": "BTC/USD"
        },{
          "proName": "BITSTAMP:ETHUSD",
          "title": "ETH/USD"
        }],
          "colorTheme": "light",
          "isTransparent": false,
          "displayMode": "regular",
          "locale": "ru"
       }`
  );

  // widget initialize at the end of document body without id or ref
  // which can be accessed easy, so we have to find it the hard way
  // to control its layout directly
  useEffect(() => {
    const frames = Array.from(document.getElementsByTagName('IFRAME'));
    const runnerFrame = frames.find(({src}) => Boolean(~src.indexOf('ticker-tape')));
    if (!runnerFrame) return;
    const container = runnerFrame.parentNode;
    setContainer(container);
  }, [loaded]);

  // delete container used to init to prevent conflict with other widgets
  // (they init in div with same class names)
  // const [removed, setRemoved] = useState(false);
  // useEffect(() => {
    // setRemoved(true);
    // alert('remove!!' + removed);
    // if (removed) return;
    // if (!loaded || removed) return;
    // const initContainer = document.getElementsByClassName('tradingview-widget-container')[0];
    // initContainer && initContainer.remove();
    // setRemoved(true);
  // }, []);/


  return (
    <div className="tradingview-widget-container">
      <div className="tradingview-widget-container__widget"/>
    </div>
  );
}

export default Runner;
