import React from "react";
import { useHistory } from 'react-router-dom';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import "./carousel.css";
import styles from './styles.module.scss';
import Button from "../../../../../../common/core/Button";
import cn from 'classnames';
import colors from "../../../../../../common/colors";
import Container from "react-bootstrap/Container";
import useNavigationToTop from "../../../../../../common/hooks/useNavigationToTop";
import {Paths} from "../../../../Menu";

function Banner() {
  return (
    <div className={styles.container}>
      <Carousel
        showThumbs={false}
        autoPlay={true}
        showStatus={false}
        stopOnHover={false}
        infiniteLoop={true}
        interval={6000}
      >
        <Slide
          title={'ВЫБЕРИТЕ СВОЙ ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ'}
          text={'Мы собрали для вас портфели наиболее прибыльных отраслей'}
          className={styles.slide1}
          link={Paths.PORTFEL}
        />
        <Slide
          title={'КАК ЭТО РАБОТАЕТ'}
          text={'При формировании портфелей используются главные принципы распределения активов'}
          className={styles.slide2}
          link={Paths.PORTFEL}
        />
        <Slide
          title={'ПРЕИМУЩЕСТВА'}
          text={'Создание условий для сохранности средств И Обеспечение высокой и стабильной прибыли'}
          className={styles.slide3}
          link={Paths.ADVANTAGES}
        />
        <Slide
          title={'ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ'}
          text={'МАКСИМАЛЬНАЯ ДОХОДНОСТЬ И ДИВЕРСИФИКАЦИЯ РИСКОВ'}
          className={styles.slide4}
          link={Paths.PORTFEL}
        />
      </Carousel>
    </div>
  );
}

function Slide({title, text, link, className}) {
  const navigate = useNavigationToTop();

  return (
    <div
      className={cn(styles.slide, className)}
    >
      <Container className="flex-column justify-content-center align-items-center">
        <div className={styles.title}>{title.toUpperCase()}</div>
        <div className={styles.text}>{text.toUpperCase()}</div>
        <Button
          title="ПОДРОБНЕЕ"
          color={colors.blue}
          onClick={() => navigate(link)}
        />
      </Container>
    </div>
  );
}

export default Banner;
