import React from "react";
import FileViewPage from "../../../FileViewPage";
import bg from "../../Main/components/Banner/images/bg2.png";

function Declaration() {
  return (
    <FileViewPage
      title="ДЕКЛАРАЦИЯ ОБ ОБЩИХ РИСКАХ"
      headerBgImage={bg}
      fileType="pdf"
      filePath={require('../../../../../docs/declaration.pdf')}
    />
  );
}

export default Declaration;
