import React from "react";
import Header from "../../../Header";
import styles from "./styles.module.scss";
import Col from "react-bootstrap/Col";
import {Image} from 'react-bootstrap';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

const iconItems = [{
  icon: require('./images/icons/1.png'),
  text: 'Эксклюзивные торговые идеи от нашей команды аналитиков',
},{
  icon: require('./images/icons/2.png'),
  text: 'Простое и удобное совершение сделок с персональным подходом',
},{
  icon: require('./images/icons/3.png'),
  text: 'Постоянный мониторинг позиций портфеля',
}];

const numberItems = [{
  text: 'Знакомство и определение финансовых целей и инвестиционного профиля',
},{
  text: 'Ваш инвестиционный консультант вместе с командой аналитиков подбирает инвестиционные идеи',
},{
  text: 'Консультант связывается с Вами и сообщает о благоприятной ситуации для покупки или продажи ценных бумаг',
},{
  text: 'Вы даете консультанту поручение на совершение сделки',
},{
  text: 'Мы отслеживаем позиции. Связываемся с Вами при необходимости пересмотра портфеля, появлении новых рекомендаций или для фиксации результата',
}];

function Assistant() {
  return (
    <>
      <Header
        title={'ПЕРСОНАЛЬНЫЙ КОНСУЛЬТАНТ'}
        className={styles.header}
      />
      <Container className={styles.brief}>
        Самостоятельная торговля на бирже требует знаний, опыта, навыков работы с торговыми терминалами, а главное времени на постоянный мониторинг ситуации и поиск инвестиционных идей.
        Персональный консультант дает Вам преимущество, сопровождая самой актуальной и необходимой информацией по рынку, предлагая рекомендации, и позволяя использовать максимум возможностей при минимуме Вашего времени.
        Десятки человек (аналитики, риск-менеджеры, трейдеры) работают на то, чтобы каждая Ваша сделка стала прибыльной.
      </Container>
      <AssistantIconList/>
      <AssistantNumberList/>
    </>
  );
}

export function AssistantIconList() {
  return (
    <div className={styles.iconItemsContainer}>
      <Container><Row className="justify-content-center">{iconItems.map(IconItem)}</Row></Container>
    </div>
  );
}

function AssistantNumberList() {
  return (
    <Container className={styles.numberItemsContainer}>
      <Row className="justify-content-center">{numberItems.map(NumberItem)}</Row>
    </Container>
  );
}

function IconItem({icon, text}) {
  return (
    <Col xs={6} lg={4} className={styles.iconItem}>
      <Image src={icon} className={styles.icon}/>
      <div className={styles.text}>{text}</div>
    </Col>
  );
}

export function NumberItem({text}, index) {
  return (
    <Col xs={12} sm={6} lg={4} className={styles.numberItem}>
      <div className={styles.number}>{index+1}</div>
      <div className={styles.text}>{text}</div>
    </Col>
  );
}

export default Assistant;
