import React, {useRef} from "react";
import Logo from "../Logo";
import {useLocation} from 'react-router-dom';
import Container from "react-bootstrap/Container";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import styles from './styles.module.scss';
import cn from 'classnames';
import DropDown from "../../../common/core/DropDown";
import Row from "react-bootstrap/Row";
import useNavigationToTop from "../../../common/hooks/useNavigationToTop";

export const Paths = {
  MAIN: '/',
  ABOUT: '/about',

  AWARDS: '/awards',
  WHY_US: '/why-us',
  ADVANTAGES: '/advantages',
  CLIENT_AGREEMENT: '/client-agreement',
  ANTI_MONEY_LAUNDERING: '/anti-money-laundering',
  PRIVACY_POLICY: '/privacy-policy',
  REGISTRATION_CERTIFICATE: '/registration-certificate',
  DECLARATION: '/declaration',
  CALENDAR: '/calendar',

  ASSISTANT: '/assistant',
  PORTFEL: '/portfel',
  FUND: '/fund',

  CAREER: '/career',
  CONTACTS: '/contacts',

  REGISTER: '/register',
  LOGIN: '/login',
};

const pageTabs = [{
  title: 'ГЛАВНАЯ',
  path: Paths.MAIN,
},{
  title: 'О НАС',
  path: Paths.ABOUT,
},{
  title: 'ФОНД',
  dropDown: [{
    name: 'Награды и достижения',
    path: Paths.AWARDS,
  },{
    name: 'Почему "НАШ ФОНД"?',
    path: Paths.WHY_US,
  },{
    name: 'Преимущества',
    path: Paths.ADVANTAGES,
  },{
    name: 'Клиентское соглашение',
    path: Paths.CLIENT_AGREEMENT,
  },{
    name: 'Борьба с отмыванием денег',
    path: Paths.ANTI_MONEY_LAUNDERING,
  },{
    name: 'Политика конфиденциальности',
    path: Paths.PRIVACY_POLICY,
  },{
    name: 'Свидетельство о регистрации',
    path: Paths.REGISTRATION_CERTIFICATE,
  },{
    name: 'Декларация об общих рисках',
    path: Paths.DECLARATION,
  },{
    name: 'Экономический календарь',
    path: Paths.CALENDAR,
  }],
},{
  title: 'УСЛУГИ',
  dropDown: [{
    name: 'Персональный консультант',
    path: Paths.ASSISTANT,
  },{
    name: 'Инвестиционный портфель',
    path: Paths.PORTFEL,
  },{
    name: 'Фонд "НАШ ФОНД"',
    path: Paths.FUND,
  },],
},{
  title: 'КАРЬЕРА',
  path: Paths.CAREER,
},{
  title: 'КОНТАКТЫ',
  className: 'mr-lg-2',
  path: Paths.CONTACTS,
}];

const estTabs = [{
  title: 'ОТКРЫТЬ СЧЁТ',
  path: Paths.REGISTER,
},{
  title: 'ВОЙТИ',
  path: Paths.LOGIN,
}];

function Menu() {
  return (
    <div className={styles.holder}>
      <Container className={cn(
        styles.container,
        'justify-content-center',
        'd-block d-lg-flex'
      )}>
        <Logo className={cn(styles.logo, 'd-none d-lg-block mr-lg-4')}/>
        <Row
          className={cn(
            styles.tabs,
            'justify-content-center'
          )}
        >
          {pageTabs.map(Tab)}
          {estTabs.map(Tab)}
        </Row>
      </Container>
    </div>
  );
}

function Tab({title, className, path, dropDown}) {
  const navigate = useNavigationToTop();
  const {pathname} = useLocation();

  // const isActive = Boolean(~pathname.indexOf(path));
  const isActive = path === pathname;

  const ref = useRef(null);

  return (
    <>
      <DropDown
        dropDown={dropDown}
        rootRef={ref}
        onTabClick={({path}) => navigate(path)}
      />
      <TouchableOpacity
        ref={ref}
        className={cn(
          styles.tab,
          isActive && styles.active,
          className
        )}
        onClick={() => {
          navigate(path);
        }}
      >
        {title}
      </TouchableOpacity>
    </>
  );
}

export default Menu;
