import React from 'react';
import Header from '../../Header';
import styles from './styles.module.scss';
import Awards from "../Main/components/Awards";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import {AdvantagesIconList, PollList} from "../FundTab/AwardsTab";
import cn from 'classnames';

// todo
function AboutGlobal() {
  return (
    <>
      <Header
        title="О НАС"
        className={styles.header}
      />
      <Awards/>
      <div className={styles.aboutWithBg}>
        <Container>
          <Row>
            <Col className="text-justify p-3" xs={12} lg={6}>
              Команда Наш Фонд – это высококвалифицированные специалисты в различных областях мировой экономики.
              Фундамент Нашего Фонда это колоссальный объём знаний и опыта которым обладает каждый наш специалист, что в совокупности формирует платформу для успешного развития инвестиционного капитала.
            </Col>
            <Col className="text-justify p-3" xs={12} lg={6}>
              В истоках Нашего Фонда лежат многочисленные победы каждого из трейдеров, аналитиков, менеджеров и тех кто сейчас представляет истеблишмент нашей компании. Революционный подход к трейдингу который применяют наши сотрудники требует каждодневной адаптации всех операций на сегодняшних рынках, то что приводит к максимальной надежности и прозрачности проводимых операций.
            </Col>
          </Row>
        </Container>
      </div>
      <Container className="d-block px-3 ma text-justify">
        <div className={styles.subheader}>ОПЫТ</div>
        <div>
          Многолетний опыт успешной деятельности европейского фонда инвестиций, помог руководству принять решение, которое позволило гражданам стран СНГ стать частью Нашего Фонда. В 2018 году, компания принимает решение об открытии подразделений в странах СНГ:
        </div>
        <Row className="mb-2 ">
          <Col xs={12} lg={6} className="d-block text-center">
            <div className={styles.year}>2018</div>
            <div className={styles.country}>Российская Федерация</div>
          </Col>
          <Col xs={12} lg={6} className="d-block text-center">
            <div className={styles.year}>2019</div>
            <div className={styles.country}>Республика Беларусь</div>
            <div className={styles.country}>Республика Казахстан</div>
          </Col>
        </Row>
        <AdvantagesIconList flex/>
        <div className={cn(styles.subheader, "pt-0")}>ДОСТИЖЕНИЯ</div>
        <div className="text-justify pb-3">
          Благодаря этим критериям Фонд завоевал почетную награду на премии Global Invest 2019 в Санкт-Петербурге и был номинирован на звание Лучшая Программа Лояльности (Best Loyalty Program) на международном форуме Trading Expo 2019 Cyprus.
        </div>
        <div className="text-justify pb-3">
          Информационная поддержка и сопровождение каждого клиента Фонда является неотъемлемой частью нашей деятельности. Каждый клиент имеет возможность воспользоваться консультацией высококвалифицированного специалиста, который разработает сбалансированный портфель, отвечающий вашим возможностям и всем потребностям. Руководствуясь высокой требовательностью клиентов, мы построили инвестиционные инструменты, отвечающие любым потребностям каждого инвестора. Многолетний опыт работы на открытых рынках показал что максимальная диверсификация активов, придает надежность и стабильность развитию капитала.
        </div>
        <div className="text-justify pb-3">
          Сегодня, мы занимаем лидирующие позиции в области формирования инвестиционных портфелей и выявления самых развивающихся секторов и корпораций всего мира. Благодаря этому в 2019 году, мы были признаны Самым быстрорастущим Инвестиционным Фондом по версии журнала Daily Trade. В 2019 году, сумма активов под управлением фонда увеличилась до 268 млн. евро, что означало прирост притока инвестиций на 58% по сравнению с 2018г.
        </div>
        <PollList/>
      </Container>
      {/*<div className={styles.subheader}>*/}
      {/*  НАШИ СПЕЦИАЛИСТЫ*/}
      {/*</div>*/}
      {/*<div className="d-flex justify-content-center pb-4"><Container className={styles.idea}>*/}
      {/*  Мы приверженцы идеи что успех на финансовых рынках достигается не только при получении экономического образования. Успешный опыт работы, управления, как своим капиталом, так и доверенными средствами, является основой для начала работы в нашей команде. С 2017-го года, торговля на рынке стала для нас площадкой для демонстрации своего профессионализма и богатых навыков.*/}
      {/*</Container></div>*/}
      {/*<About/>*/}
    </>
  );
}

export default AboutGlobal;
