import React, {useState} from "react";
import FileViewer from "react-file-viewer";
import Header from "../Header";
import './file-viewer.scss';
import { Document, Page, pdfjs } from 'react-pdf';
import useClientRect from "../../../common/hooks/useClientRect";
import Container from "react-bootstrap/Container";
import {ClipLoader} from "react-spinners";
import colors from '../../../common/colors';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

function FileViewPage({title, headerBgImage, fileType, filePath, pdfWidth=750}) {
  return (
    <>
      <Header
        title={title}
        backgroundImage={headerBgImage}
      />
      <Container className="viewer ma">
        {fileType === 'pdf' ?
          (
            <PdfViewer
              filePath={filePath}
            />
          ) : (
            <FileViewer
              fileType={fileType}
              filePath={filePath}
            />
          )
        }
      </Container>
    </>
  );
}

const WIDTH_FACTOR=0.8;
const MAX_WIDTH=750;

export function PdfViewer({filePath, widthFactor=WIDTH_FACTOR}) {
  const [pdfPagesNumber, setPdfPagesNumber] = useState(0);

  function onPdfLoaded({numPages}) {
    setPdfPagesNumber(numPages);
  }

  const {width: clientWidth} = useClientRect();

  const width = clientWidth*widthFactor;

  return (
    <Document
      file={filePath}
      onLoadSuccess={onPdfLoaded}
      loading={<ClipLoader size={30} color={colors.blue}/>}
    >
      {[...Array(pdfPagesNumber)].map((_, i) => (
        <Page
          pageNumber={i+1}
          width={width > MAX_WIDTH ? MAX_WIDTH : width}
        />
      ))}
    </Document>
  );
}

export default FileViewPage;
