import React, {useEffect} from "react";
import useScript from "../../../../../common/hooks/useScript";
import Header from "../../../Header";
import bg from '../../Career/images/bg.png';

const HEADER_HEIGHT = '22rem';
const CALENDAR_HEIGHT = 600;
const CALENDAR_WIDTH = '85%';
const CALENDAR_MARGIN_Y = 20;

function Calendar({setContainer}) {
  const [loaded] = useScript(
    'https://s3.tradingview.com/external-embedding/embed-widget-events.js',
    `{
          "colorTheme": "light",
          "isTransparent": false,
          "width": "${CALENDAR_WIDTH}",
          "height": "${CALENDAR_HEIGHT}",
          "locale": "ru",
          "importanceFilter": "-1,0,1"
        }`
  );

  useEffect(() => {
    if (!loaded) return;
    const frames = Array.from(document.getElementsByTagName('IFRAME'));
    const calendarFrame = frames.find(({src}) => Boolean(~src.indexOf('events')));
    if (!calendarFrame) return;
    const container = calendarFrame.parentNode;
    calendarFrame.style.width = '100%';
    container.style.position = 'absolute';
    container.style.top = HEADER_HEIGHT;
    container.style.zIndex = 1;
    container.style.left = 0;
    container.style.right = 0;
    container.style.margin = `${CALENDAR_MARGIN_Y}px auto`;
    setContainer(container);
  }, [loaded]);

  return (
    <>
      <Header
        title="ЭКОНОМИЧЕСКИЙ КАЛЕНДАРЬ"
        backgroundImage={bg}
        style={{marginBottom: CALENDAR_HEIGHT + CALENDAR_MARGIN_Y*2}}
      />
      <div className="tradingview-widget-container">
        <div className="tradingview-widget-container__widget"/>
      </div>
    </>
  );
}

export default Calendar;
