import React from "react";
import FileViewPage from "../../../FileViewPage";
import bg from "../../Main/components/Banner/images/bg4.png";

function RegistrationCertificate() {
  return (
    <FileViewPage
      title="СВИДЕТЕЛЬСТВО О РЕГИСТРАЦИИ"
      headerBgImage={bg}
      fileType="pdf"
      filePath={require('../../../../../docs/certificate_fm.pdf')}
    />
  );
}

export default RegistrationCertificate;
