import React, {useCallback, useState} from "react";
import styles from './style.module.css';
import Wrapper from "../core/Wrapper";
import useEscape from "../hooks/useEscape";
import {useDropzone} from "react-dropzone";
import Image from "react-bootstrap/Image";
import cn from 'classnames';
import Button from "../core/Button";
import useWaiting from "../hooks/useWaiting";

export function useUploadAvatarModal(onSave=async()=>{}) {
  const [isUploadAvatarModalOpen, setIsUploadAvatarModalOpen] = useState(false);
  // const [isUploadAvatarModalOpen, setIsUploadAvatarModalOpen] = useState(true);
  const openAvatarModal = () => {
    setIsUploadAvatarModalOpen(true);
  };
  const avatarModal = isUploadAvatarModalOpen && (
    <UploadAvatarModal
      onClose={() => setIsUploadAvatarModalOpen(false)}
      onSave={onSave}
    />
  );

  return {openAvatarModal, avatarModal};
}

const UploadAvatarModal = ({onClose, onSave=async()=>{}}) => {
  const containerRef = useEscape(styles.container, onClose);

  const [previewSrc, setPreviewSrc] = useState(null);
  const [file, setFile] = useState(null);

  const [waiting, uploadAvatar] = useWaiting(onSave);

  const onSaveClick = async () => {
    if (!file) return;
    await uploadAvatar(file);
    onClose();
  };

  const onDrop = useCallback(acceptedFiles => {
    const file = acceptedFiles[0];
    if (file) {
      setFile(file);
      preview(file);
    } else {
      // error
    }
  }, []);

  const preview = (file) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      setPreviewSrc(reader.result);
    };
    reader.readAsDataURL(file);
  };

  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject
  } = useDropzone({
    onDrop,
    multiple: false,
    accept: 'image/*',
    //...
  });

  return (
    <Wrapper
      className={styles.container}
      ref={containerRef}
    >
      <div className={styles.box}>
        <div className={styles.header}>
          Загрузить аватар
        </div>
        <div className={styles.main}>
          <Preview
            imageSrc={previewSrc}
          />
          <div className={styles.rightSideContainer}>
            <div {...getRootProps({
              className: cn(
                styles.uploadBox,
                isDragAccept && styles.uploadBoxAccept,
                isDragReject && styles.uploadBoxReject,
              )})}>
              <input {...getInputProps({className: styles.input})} required/>
              <span>
                {
                  isDragReject ? 'Вы можете загрузить только изображение' :
                  "Перетащите свою фотографию сюда, или нажмите, чтобы выбрать"
                }
              </span>
            </div>
            <div className={styles.buttonsContainer}>
              <Button
                type={'submit'}
                width={140}
                onClick={onSaveClick}
                title={'СОХРАНИТЬ'}
                color={'white'}
                waiting={waiting}
                waitingColor={'rgba(255,255,255,0.35)'}
                // backgroundColor={'#3B509F'}
              />
              <Button
                type={'button'}
                className={styles.rightButton}
                width={120}
                onClick={onClose}
                title={'ОТМЕНА'}
                color={'white'}
                waiting={waiting}
                waitingColor={'rgba(255,255,255,0.35)'}
                // backgroundColor={'#AA2D0D'}
              />
            </div>
          </div>
        </div>
      </div>
    </Wrapper>
  );
};

const Preview = ({imageSrc}) => {
  return (
    <div
      className={styles.avatarContainer}
      style={{
        border: imageSrc ? '1px solid rgba(204, 204, 204, 0.1)' : 'none',
      }}
    >
      {imageSrc ? (
        <Image
          fluid
          className={styles.avatarImage}
          src={imageSrc}
        />
      ) : (
        <AvatarPlaceholder/>
      )}
    </div>
  );
};

export const AvatarPlaceholder = ({fontSize=75, className, ...props}) => (
  <div className={cn(styles.avatarPlaceholder, className)} {...props}>
    <i
      style={{fontSize}}
      className={cn("fas fa-camera", styles.avatarPlaceholderIcon)}
    />
  </div>
);

export default UploadAvatarModal;
