import React from "react";
import Header from "../../../Header";
import styles from './styles.module.scss';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../../../../common/core/TouchableOpacity";
import useNavigationToTop from "../../../../../common/hooks/useNavigationToTop";
import {Paths} from "../../../Menu";

function Portfel() {
  return (
    <>
      <Header
        title="ИНВЕСТИЦИОННЫЙ ПОРТФЕЛЬ"
        className={styles.header}
      />
      <Container className={styles.container}>
        <div className={styles.subheader}>
          Инвестиционный портфель – максимальная доходность и диверсификация рисков
        </div>
        <div className="text-justify">
          У инвестиционных портфелей две основные задачи: обеспечение максимальной стабильной прибыли и минимизация возможности потери активов. Наши специалисты распределяют капитал по разным продуктам и инструментам, надежно защищая средства и обеспечивая доход выше среднего по рынку.
        </div>
        <div className={styles.subheader}>
          ПРЕИМУЩЕСТВА
        </div>
        <Row className="px-2">
          <AdvantageItem
            icon={require('./images/icons/1.png')}
            header="Создание условий для сохранности средств."
            text="Когда одни инструменты и продукты, входящие в портфель, приносят убытки, то прибыль от других их полностью компенсирует. Этот принцип работает в любых рыночных условиях."
          />
          <AdvantageItem
            icon={require('./images/icons/2.png')}
            header="Обеспечение высокой и стабильной прибыли."
            text="Специалисты вкладывают деньги в надежные и высокодоходные инструменты."
          />
        </Row>
        <div className={styles.subheader}>
          КАК ЭТО РАБОТАЕТ?
        </div>
        <div className="text-justify mb-4">
          При формировании портфелей используются главные принципы распределения активов. Прежде всего, большая доля средств вкладывается в один или несколько основных активов, отличающихся стабильностью и высокой ликвидностью. Еще одна часть работает в активах компаний, зарекомендовавших себя как наиболее надежные участники рынка, это позволяет защитить капитал от инфляции. И третья, минимальная часть портфеля, инвестируется в высокодоходные, но рискованные активы. Они могут принести высокую прибыль, но в случае неудачи, потеря не отразится на общем балансе.
        </div>
        <div className="text-justify mb-2">
          Мы собрали для вас портфели наиболее прибыльных отраслей. Распределение активов осуществляется с учетом всех факторов, которые могут повлиять на ситуацию на рынке. Эксперты используют надежные и проверенные схемы, чтобы Ваши инвестиции приносили максимально высокую прибыль.
        </div>
        <div className={styles.divider}/>
        <OpenAccountButton/>
        <div className="text-center">
          Выберите свой Инвестиционный портфель
        </div>
      </Container>
    </>
  );
}

export function OpenAccountButton() {
  const navigate = useNavigationToTop();

  return (
    <TouchableOpacity
      className={styles.openAccount}
      onClick={() => {
        navigate(Paths.REGISTER);
      }}
    >
      ОТКРЫТЬ СЧЕТ
    </TouchableOpacity>
  );
}

function AdvantageItem({icon, header, text}) {
  return (
    <Col xs={12} sm={6} className={styles.advantageItem}>
      <Image src={icon} className={styles.icon}/>
      <div className={styles.subsubheader}>
        {header}
      </div>
      <div>
        {text}
      </div>
    </Col>
  );
}

export default Portfel;
