import Image from "react-bootstrap/Image";
import React from "react";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import useNavigationToTop from "../../../common/hooks/useNavigationToTop";
import {Paths} from "../Menu";

function Logo({violet=false, navigatable=true, ...props}) {
  const navigate = useNavigationToTop();

  return (
    <TouchableOpacity
      {...props}
      onClick={() => navigatable && navigate(Paths.MAIN)}
    >
      <Image
        src={require(violet ? './logo_violet.png' : './logo.png')}
        style={{width: '100%'}}
      />
    </TouchableOpacity>
  );
}

export default Logo;
