import React from 'react';
import styles from './styles.module.scss';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import Logo from "../Logo";
import cn from 'classnames';

function Footer() {
  return (
    <div className={styles.container}>
      <Container className="flex-column">
        <Row>
          {addresses.map(Address)}
        </Row>
        <div className={styles.contact}>
          <Logo
            violet
            className={cn(styles.logo, "d-none d-lg-flex")}
          />
          <Contacts/>
        </div>

        {/*<div className={styles.subfooter}>*/}
        {/*  Этот сайт управляется компанией FRONTIER MARKETS LTD регистрационный номер <strong><a href={require('../../../docs/certificate_fm.pdf')}>10912941</a></strong> которая зарегистрирована по адресу 28 Monkville Avenue, London, United Kingdom регулируется финансовым регулирующим органом FCA 632380 принадлежит и управляется*/}
        {/*  группой компании Dragon Capital LTD, регистрационный номер <strong><a href={require('../../../docs/license.pdf')}>24303</a></strong> которая зарегистрирована по адресу Suite 305, Griffith Corporate Center, P.O. Box: 1510, Beachmont Kingstown, St. Vincent and Grenadines предоставляет сервис по операциям в Личном кабинете на условиях публичной оферты. (с) Все права защищены 2018-2020*/}
        {/*</div>*/}
        <div className={styles.subfooter}>
          investfondru.com предоставляет сервис по операциям в Личном кабинете на условиях публичной оферты (с) Все права защищены 2018-2020
        </div>
      </Container>
    </div>
  );
}

function Contacts() {
  return (
    <div className="d-flex align-items-center">
      <TouchableOpacity onClick={() => window.open(`https://t.me/${'telegram'}`, '_blank')}>
        <Image src={require('./images/telegram.png')} className={styles.icon}/>
      </TouchableOpacity>
      <TouchableOpacity onClick={() => window.open(`https://api.whatsapp.com/send?phone=${'whatsapp'}`, '_blank')}>
        <Image src={require('./images/whatsapp.png')} className={styles.icon}/>
      </TouchableOpacity>
    </div>
  );
}

const addresses = [{
  title: 'РОССИЯ',
  address: 'Пресненская наб., 12, Москва, 123317',
  telephone: '+7 499 681 34 18',
  email: 'support@investfondru.com',
},{
  title: 'ВЕЛИКОБРИТАНИЯ',
  address: 'Suite 305, Griffith Corporate Center, P.O. Box: 1510, Beachmont Kingstown, St. Vincent and Grenadines',
  telephone: '+7 499 681 34 18',
  email: 'support@investfondru.com',
}];

function Address({title, address, telephone, email}) {
  return (
    <Col xs={12} lg={6} className={styles.address}>
      <div className={styles.title}>{title}</div>
      <div className={styles.info}>
        <Image src={require('./images/icon_place.png')}/>
        {address}
      </div>
      <div className={styles.info}>
        <Image src={require('./images/icon_phone.png')}/>
        {telephone}
      </div>
      <div className={styles.info}>
        <Image src={require('./images/icon_email.png')}/>
        {email}
      </div>
    </Col>
  );
}

export default Footer;
