import React from "react";
import FileViewPage from "../../../FileViewPage";
import bg from '../../Main/components/Banner/images/bg3.png';

function PrivacyPolicy() {
  return (
    <FileViewPage
      title="ПОЛИТИКА КОНФИДЕНЦИАЛЬНОСТИ"
      headerBgImage={bg}
      fileType="pdf"
      filePath={require('../../../../../docs/ПОЛИТИКА_КОНФИДЕНЦИАЛЬНОСТИ.pdf')}
    />
  );
}

export default PrivacyPolicy;
