import React from "react";
import FileViewPage from "../../../FileViewPage";
import bg from '../../Main/components/Banner/images/bg1.png';

function AntiMoneyLaundering() {
  return (
    <FileViewPage
      title="БОРЬБА С ОТМЫВАНИЕМ ДЕНЕГ"
      headerBgImage={bg}
      fileType="pdf"
      filePath={require('../../../../../docs/Борьба_с_отмыванием_денег.pdf')}
    />
  );
}

export default AntiMoneyLaundering;
