import React from "react";
import styles from './styles.module.scss';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Header from "../../../Header";
import {OpenAccountButton} from "../Portfel";

const howItWorks = [{
  title: 'ВЫ ВЫБИРАЕТЕ ФОНД',
  text: 'Вкладываете в него деньги, передавая их в профессиональное управление с целью получения дохода.',
},{
  title: 'СРЕДСТВА ФОНДА ИНВЕСТИРУЮТСЯ',
  text: 'В инструменты финансового рынка. Управляющая команда заботится о сохранении и приумножении активов фонда.',
},{
  title: 'ВЫ ПОЛУЧАЕТЕ ДОХОД',
  text: 'Доход образуется в случае роста капитализаций фонда. Однако такой рост не гарантирован. Капитализация может снижаться.',
}];

const advantages = [{
  title: 'ДОСТУПНЫЕ И УДОБНЫЕ',
  text: 'Минимальная сумма вложений от 50000 рублей (в Личном кабинете). Пополнение открытых фондов в любой момент',
},{
  title: 'ВЫСОКОЛИКВИДНЫЕ',
  text: 'Вы можете вывести инвестированные деньги в любой рабочий день',
},{
  title: 'УПРАВЛЯЮТСЯ ПРОФЕССИОНАЛАМИ',
  text: 'В “НАШ ФОНД”  Управление Активами одна из лучших команд управляющих Инвестиционными фондами',
}];

function Fund() {
  return (
    <>
      <Header
        title='Фонд "НАШ ФОНД"'
        className={styles.header}
      />
      <Container className={styles.container}>
        <div className={styles.brief}>
          Инвестиционные фонды – это простой и доступный способ вложения
          денег с помощью профессиональных управляющих в ценные бумаги с
          возможностью получения большего дохода, чем по вкладам.
          Вложения в инвестиционные фонды удобны для тех, кто не готов
          самостоятельно принимать инвестиционные решения из-за
          недостатка времени или опыта.
        </div>
        <div className={styles.subheader}>
          КАК ЭТО РАБОТАЕТ?
        </div>
        <Row>
          {howItWorks.map(NumberItem)}
        </Row>
        <div className={styles.subheader}>
          ПРЕИМУЩЕСТВА
        </div>
        <Row>
          {advantages.map(NumberItem)}
        </Row>
        <div className={styles.divider}/>
        <OpenAccountButton/>
        <div className="text-center">
          Выберите ИФ по уровню риска/доходности
        </div>
      </Container>
    </>
  );
}

function NumberItem({title, text}, index) {
  return (
    <Col
      xs={12} lg={4}
      className={styles.numberItem}
    >
      <div className={styles.number}>{index+1}</div>
      <div className={styles.title}>{title}</div>
      <div className={styles.text}>{text}</div>
    </Col>
  );
}

export default Fund;
