import React, {useState} from "react";
import Header from "../../Header";
import styles from './styles.module.scss';
import Modal from "../../../../common/core/Modal";
import Col from "react-bootstrap/Col";
import {Form, Text, TextArea} from "informed";
import Row from "react-bootstrap/Row";
import cn from "classnames";
import TouchableOpacity from "../../../../common/core/TouchableOpacity";
import Image from "react-bootstrap/Image";
import arrowIconPath from "../../../OldLanding/contents/Contacts/images/icon_go.png";
import Container from "react-bootstrap/Container";

function Contacts() {
  const [modalOpen, setModalOpen] = useState(false);

  const sendContactMailToAdmin = async (formState) => {
    // todo send
    setModalOpen(true);
  };

  return (
    <>
      <Header
        title="КОНТАКТЫ"
        className={styles.header}
      />

      <Modal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        className={styles.modal}
      >
        Ваше сообщение было успешно отправлено.
      </Modal>

      <div className={styles.title}>
        СВЯЖИТЕСЬ С НАМИ
      </div>

      <Container className={styles.formContainer}>
        <Form onSubmit={sendContactMailToAdmin}>
          <Row className={styles.form}>
            <Col xs={12}>
              <Text className={styles.input} field={'title'} placeholder={'Тема'} required/>
            </Col>
            <Col xs={12}>
              <TextArea className={styles.area} field={'text'} placeholder={'Текст'} required/>
            </Col>
            <Col xs={12} md={6} lg={2}>
              <Text className={styles.input} field={'firstName'} placeholder={'Имя'} required/>
            </Col>
            <Col xs={12} md={6} lg={2}>
              <Text className={styles.input} field={'secondName'} placeholder={'Фамилия'} required/>
            </Col>
            <Col xs={12} md={4} lg={3}>
              <Text className={styles.input} field={'phone'} placeholder={'Ваш телефон'} required/>
            </Col>
            <Col xs={12} md={8} lg={5}>
              <Text className={cn(styles.input, styles.email)} field={'email'} placeholder={'Ваш email'} type={'email'} required/>
              <TouchableOpacity className={styles.button}>
                <button style={{width: '100%', height: '100%', backgroundColor: 'transparent', border: 'none', outline: 'none', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                  <Image src={arrowIconPath}/>
                </button>
              </TouchableOpacity>
            </Col>
          </Row>
        </Form>

      </Container>
    </>
  );
}

export default Contacts;
