import React, {useContext} from 'react';
import s from './s.module.scss';
import cn from 'classnames';
import Wrapper from "../../../common/core/Wrapper";
import Image from "react-bootstrap/Image";
import backIconPath from '../images/slide.png';
import settingsIconPath from '../images/settings.png';
import Logo from "../../Landing/Logo";
import TouchableOpacity from "../../../common/core/TouchableOpacity";
import menuIconPath1 from '../images/menu1.png';
import menuIconPath2 from '../images/menu2.png';
import menuIconPath3 from '../images/menu3.png';
import menuIconPath4 from '../images/menu4.png';
import NetworkContext from "../../../contexts/NetworkContext";
import DataContext from "../../../contexts/DataContext";
import {useUploadAvatarModal} from "../../../common/UploadAvatarModal";

const icons = [
  <Image width={25} className={s.icon} src={menuIconPath1}/>,
  <Image width={25} className={s.icon} src={menuIconPath2}/>,
  <Image width={25} className={s.icon} src={menuIconPath3}/>,
  <Image width={25} className={s.icon} src={menuIconPath4}/>,
  <i className="fas fa-user" style={{
    fontSize: 25,
    color: '#637de0',
  }}/>,
  <i className="fas fa-users-cog" style={{
    fontSize: 25,
    color: '#637de0',
  }}/>
];

const AVATAR_SIZE = 100;

function User() {
  const {user} = useContext(DataContext);
  const {firstName, secondName} = user.data;

  // todo onsave
  const {openAvatarModal, avatarModal} = useUploadAvatarModal();

  return (
    <div className={s.user}>
      {avatarModal}
      <TouchableOpacity
        onClick={openAvatarModal}
        className={s.avatarContainer}
        style={{width: AVATAR_SIZE, minWidth: AVATAR_SIZE, height: AVATAR_SIZE}}
      >
        <AvatarPlaceholder fontSize={AVATAR_SIZE/3}/>
      </TouchableOpacity>
      <div className={s.name}>
        {`${firstName} ${secondName}`}
      </div>
    </div>
  );
}

export const AvatarPlaceholder = ({fontSize=75, className, ...props}) => (
  <div className={cn(s.avatarPlaceholder, className)} {...props}>
    <i
      style={{fontSize}}
      className={cn("fas fa-camera", s.avatarPlaceholderIcon)}
    />
  </div>
);

const Menu = ({tabsInfo, activeTabIndex, setActiveTabIndex}) => {
  const {onLogout} = useContext(NetworkContext);

  const tabs = tabsInfo.map(({name}, index) => (
    <TouchableOpacity
      key={name}
      className={cn(s.tab, index===activeTabIndex && s.active)}
      onClick={() => setActiveTabIndex(index)}
    >
      <div className={s.iconContainer}>
        {icons[index]}
      </div>
      <div className={s.text}>{name.toUpperCase()}</div>
    </TouchableOpacity>
  ));

  return (
    <Wrapper className={s.container}>
      <div className={s.menu}>
        <Logo
          navigatable={false}
          className={s.logo}
        />
        <User/>
        {tabs}
      </div>
      <div className={s.logout}>
        <TouchableOpacity onClick={onLogout}>
          <Image width={32} src={backIconPath}/>
        </TouchableOpacity>
      </div>
    </Wrapper>
  );
};

export default Menu;
